:root {
  --primary-font-family: "DM Sans", sans-serif;
  --secondary-font-family: "Titan One", sans-serif;
  --primary-accent-color: #d41443;
  --secondary-accent-color: #ffa5ba;
  --text-color-theme-dark: #fff;
  --text-color-theme-light: #000;
  --text-color-secondary: #907e82;
  --accent-bg-color: #fff5f6;
  --section-border-radius: 24px;
  --primary-cubic-bezier: cubic-bezier(.4, 0, .2, 1);
}

.gallery__list, .mobile-menu__btn, .mobile-menu__btn--open {
  justify-content: center;
  align-items: center;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.page-header__container {
  background-color: #ffb8ca;
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  display: flex;
}

.page-header__navigation {
  display: flex;
}

.page-header__menu, .page-header__mobile-menu-btn {
  display: none;
}

@media screen and (min-width: 768px) {
