.mobile-menu {
  width: 250px;
  min-height: 100vh;
  opacity: .3;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 400ms var(--primary-cubic-bezier), visibility 400ms var(--primary-cubic-bezier), transform 500ms var(--primary-cubic-bezier);
  background-color: #e17992;
  padding-top: 60px;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  z-index: 1;
  transform: translateX(0);
}

.mobile-menu__btn--open {
  width: 50px;
  height: 50px;
  background-color: var(--primary-accent-color);
  text-align: center;
  border-radius: 50%;
  margin-bottom: 12px;
  margin-left: auto;
  margin-right: auto;
}

.mobile-menu__btn {
  width: 209px;
  height: 44px;
  background-color: var(--text-color-theme-dark);
  text-align: center;
  transition: box-shadow 300ms var(--primary-cubic-bezier);
  border-radius: 22px;
  margin-bottom: 12px;
  margin-left: auto;
  margin-right: auto;
}

.mobile-menu__btn:hover {
  box-shadow: 0px 0px 5px 5px rgba(212, 20, 68, .5), 0px 0px 0px 0px var(--primary-accent-color);
}

.mobile-menu__btn--link {
  color: var(--primary-accent-color);
  transition: transform 300ms var(--primary-cubic-bezier);
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
  text-decoration: none;
  transform: scale(1);
}

.mobile-menu__btn:hover .mobile-menu__btn--link {
  transform: scale(1.1);
}

.mobile-menu__btn--svg {
  width: 9px;
  height: 9px;
  stroke: var(--primary-accent-color);
  stroke-width: 2px;
  fill: rgba(0, 0, 0, 0);
  transition: transform 300ms var(--primary-cubic-bezier);
  margin-left: 24px;
  transform: scale(1);
}

.mobile-menu__btn:hover .mobile-menu__btn--svg {
  transform: scale(1.1);
}

.mobile-menu__list {
  width: 100%;
}

.mobile-menu__item {
  border-bottom: 1px solid #e18298;
}

.mobile-menu__item:nth-child(1) {
  border-top: 1px solid #e18298;
}

.mobile-menu__link {
  color: var(--text-color-theme-dark);
  transition: color 250ms var(--primary-cubic-bezier);
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28571;
  text-decoration: none;
  display: block;
}

.mobile-menu__link.mobile-menu__link--current, .mobile-menu__link:hover, .mobile-menu__link:focus {
  color: var(--primary-accent-color);
}

.close__btn {
  width: 20px;
  height: 20px;
  transition: background-color 250ms var(--primary-cubic-bezier), transform 250ms var(--primary-cubic-bezier);
  border-radius: 50%;
  position: absolute;
  top: 20px;
  right: 20px;
  transform: rotate(90deg);
}

.close__btn:hover, .close__btn:focus {
  background-color: #e18298;
  transform: rotate(-90deg);
}

.close__btn--svg {
  width: 14px;
  height: 14px;
  stroke: var(--text-color-theme-dark);
  stroke-width: 2px;
  transition: stroke 250ms var(--primary-cubic-bezier);
}

.close__btn:hover .close__btn--svg, .close__btn:focus .close__btn--svg {
  stroke: var(--primary-accent-color);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    width: 40%;
  }
}

@media screen and (min-width: 1280px) {
  .mobile-menu {
    display: none;
  }
